* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Encode Sans Expanded', sans-serif;
}

::-webkit-scrollbar{
  outline-offset: -10px;
  white-space: normal;
  width: 12px;
}

::-webkit-scrollbar-track{
  background: #010606;
}

::-webkit-scrollbar-thumb{
  background: #1282a2;
  border-radius: 25px;
}